<template>
  <div>
    <div v-if="isInstallMetaMask">
      <div class="titlefont fontLargeSize titleFont">
        메타마스크 설치됨
      </div>
      <div id="tempImage">

      </div>
  
      <div v-if="myAddress == null">
        <button v-on:click="connectToMetaMask">메타마스크 연결</button>
      </div>
      <div v-else>
        <!--메타마스크 연결이 되었을 때,  -->
        <div v-if="myContract == null ">
          <button v-on:click="connectToContract">컨트랙트 연결</button>
        </div>
        <div v-else>
          <!-- 컨트랙트까지 연결이 되었다 -->
          <!-- ownerOf 구현 -->

          <div class = "function">
            <div class="title">
              ownerOf
            </div>
            <div class="parameters">
              <span class="parameterName">tokenId : </span>
              <input class="parameterInput" type="text" placeholder="tokenId" v-model="ownerOf_tokenId" />
            </div>

            <div class="buttons">
              <button class="callButton" v-on:click="requestOwnerOf">call</button>
            </div>

            <div class="result" v-if="ownerOf_result != null">
              ownerOf : {{ownerOf_result}}
            </div>
          </div>
          <!-- balanceOf 구현 -->
          <div class="function">
            <div class="title">
              balanceOf
            </div>

            <div class="parameters">
              <span class="parameterName">owner : </span>
              <input class="parameterInput" type="text" placeholder="owner" v-model="balanceOf_address" />
            </div>
            <div class="buttons">
              <button class="callButton" v-on:click="requestBalanceOf">call</button>
            </div>
            <div class="result" v-if="balanceOf_result != null">
              balanceOf : {{balanceOf_result}}
            </div>
          </div>

          <div class="function">
            <div class="title">
              mint
            </div>

            <div class="parameters">
              <span class="parameterName">to : </span>
              <input class="parameterInput" type="text" placeholder="to" v-model="mint_to" />
            </div>

            <div class="parameters">
              <span class="parameterName">tokenId : </span>
              <input class="parameterInput" type="text" placeholder="tokenId" v-model="mint_tokenId" />
            </div>

            <div class="parameters">
              <span class="parameterName">uri : </span>
              <input class="parameterInput" type="text" placeholder="uri" v-model="mint_uri" />
            </div>

            <div class="buttons">
              <button class="callButton" v-on:click="requestMint">call</button>
            </div>
            <div class="result" v-if="mint_result != null">
              mint : {{mint_result}}
            </div>
          </div>
          <div>
  
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      메타 마스크 설치안됨
    </div>
  </div>
</template>

<script>
import {ethers} from "ethers";
import abi from "../assets/abi.json";

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      isInstallMetaMask : window.ethereum != null 
                          && window.ethereum.isMetaMask == true,
      myAddress : null,
      myContract : null,

      ownerOf_tokenId : null,
      ownerOf_result : null,
      
      balanceOf_address : null,
      balanceOf_result : null,

      mint_to : null,
      mint_tokenId : null,
      mint_uri : null,
      mint_result : null,
    }
  },
  methods:{
    async connectToMetaMask()
    {
      console.log('click connectToMetaMask');
      var addressArray = await window.ethereum.request( { method : "eth_requestAccounts" } );
      
      console.log(addressArray);

      // 주소값이 배열로 들어있다.
      if ( addressArray.length > 0 )
      {
        this.myAddress = addressArray[0];
      }
    },
    connectToContract()
    {
      console.log("click connectToContract");
      var contractAddress = "0xc52bcCB5cbE0D0EB4068E54decA1Ae825f95965A";

      var provider = new ethers.providers.Web3Provider(window.ethereum);
      var signer = provider.getSigner();
      this.myContract = new ethers.Contract(contractAddress, abi, signer);
      console.log(this.myContract);
    },
    async requestOwnerOf()
    {
      console.log("click requestOwnerof");
      console.log(this.ownerOf_tokenId);
      this.ownerOf_result = await this.myContract.ownerOf(this.ownerOf_tokenId);
      console.log(this.ownerOf_result);
    },
    async requestBalanceOf()
    {
      console.log("click requestBalanceOf");
      console.log(this.balanceOf_address);
      this.balanceOf_result = await this.myContract.balanceOf(this.balanceOf_address);
      console.log(this.balanceOf_result);
    },
    async requestMint()
    {
      console.log("click requstMint");
      console.log(this.mint_to);
      console.log(this.mint_tokenId);
      console.log(this.mint_uri);
      this.mint_result = await this.myContract.mint(
        this.mint_to, this.mint_tokenId, this.mint_uri);
      console.log(this.mint_result);
    }
    
  }
}
</script>

